*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.logoSearchWrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    width: clamp(150px, 30%, 300px);
    
    &>img{
        filter: invert(100%);
    }

    
}
.painelNav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2% 4%;
    background-color: black;
    width: 100vw;
    
        &>img{
            filter: invert(100%);
        }
    
    
    &>*{
        width: 33%;
    }
    
}
.searchWrapper{
    display: flex;
    align-items: center;
    background-color: white;
    border: solid 1px black;
    width: 100%;
    border-radius: 15px;
    padding: 0rem 1rem;
    
}
#searchBar {
    background-color: white;
    border: none;
    outline: none;
}

.mobileMenu{
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
}

 .mobileMenu>ul{
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    background-color: black;
    transition: all 1s ease-in-out;
}
.opacity100{
   opacity: 1;
}
.opacity0{
    opacity: 0;
}

.navItem{
    text-decoration: none;
    color: white;
    z-index: 999;
}

button{
    z-index: 999;
}
.painelNav>.navWrapper{
    width: 30%;
    &>ul{
        display: flex;
        justify-content: space-evenly;
        z-index: 1;
    }
}

.tripWrapper{
    font-size: clamp(9pt, 2vw, 12pt);
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 0.2rem;
    background-color: rgb(250, 250, 250);
    border-radius: 10px;
    width: 100%;
    height: auto;
    &>div{
        text-align: center;
        width: clamp(300px, 80vw, 600px);
    }
    &>:nth-child(3){
        width: 130px;
    }
    &>:nth-child(1){
        width: 130px;
    }
    &>div>img{
            width: clamp(80px, 5vw, 110px);
    }
    overflow: hidden;
    
}

.tripsPageWrapper{
    display: flex;
    width: 70vw;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
}

.btnApply{
    background-color: #000;
    color: white;
    width: 100px;
    aspect-ratio: 6/2;
    border-radius: 15px;
}

datalist{
    background-color: rgb(240, 240, 240);
}
#createForm{
    height: auto;
    gap: 0.3rem;
    padding: 1rem;
}

.errMsg{
    font-size: 8pt;
    color: red;
}

@media screen and (min-width: 1024px) {
    .mobileMenu{
        display: none;
        &>ul{
            display: none;
            z-index: 1;
            
        }
    }
    #ulMenu{
        display: none;
        pointer-events: none;
        background-color: red;
    }
    .tripWrapper {
        flex-direction: row;
        height: 180px;
    }
    
}